body {
    margin: 0;
    padding: 0;
    /*overflow-y: hidden;*/
}



.page
{
}

.panel {
    width: 1000px;
    margin: 300px auto;
}

.player {
    display: block;
}

.page_hidden {
    display: none;
    pointer-events: none;
}

.page_content_form
{
}

.page_content_player {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.panel__confirm {
    display: block;
    width: 200px;
    height: 50px;
    margin: 0 auto;
    font-size: 18px;
}

.panel__input {
    display: block;
    margin: 10px;
    width: 100%;
    font-family: sans-serif;
    font-size: 18px;
    appearance: none;
    box-shadow: none;
    border-radius: none;
    padding: 10px;
    border: solid 1px #dcdcdc;
    border-bottom: solid 2px #c9c9c9;
    transition: border 0.3s;
    &:focus {
        outline: none;
        border-bottom: solid 2px #969696;
    }

}

.player__controls {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: flex-start;
}

.player__play {
    width: 50px;
    height: 30px;
    background: black;
    color: white
}

.player__screen
{
}

.player__stop {
    width: 50px;
    height: 30px;
    background: red;
    color: white
}
